.cButton{min-height:36px;min-width:30px;color:#fff;font-size:14px;font-weight:500;line-height:2.5em;border-radius:4px;box-shadow:none;border:none;cursor:pointer;font-family:Noto Sans,sans-serif}.cButton.Disabled{cursor:default;pointer-events:none}.cButton.Info{background-color:#0959d9;transition:all .3s ease-out}.cButton.Info:hover{background-color:#1678ff}.cButton.Info:active{background-color:#012c8c}.cButton.Info:focus{background-color:#0959d9;border:#012C8C 1px solid}.cButton.Info.Disabled{background-color:#bae0ff}.cButton.Primary{background-color:#019cb0;transition:all .3s ease-out}.cButton.Primary:hover{background-color:#00b8d9}.cButton.Primary:active{background-color:#097984}.cButton.Primary:focus{background-color:#019cb0;border:#0A6972 1px solid}.cButton.Primary.Disabled{background-color:#e3ecea}.cButton.Destructive{background-color:#d01322;transition:all .3s ease-out}.cButton.Destructive:hover{background-color:#f44336}.cButton.Destructive:active{background-color:#a8071a}.cButton.Destructive:focus{background-color:#d01322;border:#820214 1px solid}.cButton.Destructive.Disabled{background-color:#e7e7e7}.cButton.Warning{background-color:#d48906;transition:all .3s ease-out}.cButton.Warning:hover{background-color:#ff9800}.cButton.Warning:active{background-color:#874d00}.cButton.Warning:focus{background-color:#d48906;border:#874D00 1px solid}.cButton.Warning.Disabled{background-color:#d4890633}.cButton.Safe{background-color:#389e0d;transition:all .3s ease-out}.cButton.Safe:hover{background-color:#4caf50}.cButton.Safe:active{background-color:#237803}.cButton.Safe:focus{background-color:#389e0d;border:#135200 1px solid}.cButton.Safe.Disabled{background-color:#389e0d33}
